import Vuex from 'vuex'
import Vue from 'vue'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    isAdmin: false,
    calendar_setting: {},
    chatLog: {private: {}, group: {}}
  },
  mutations: {
    updateIsAdmin(state, value) {
      state.isAdmin = value
    },
    updateCalendar(state, value) {
      state.calendar_setting = value
    },
    flushChatLog(state, data) {
      state.chatLog = data
    },
    pushChatLog(state, data) {
      let r = data['tableName'].split('_')
      if (state.chatLog[r[0]][r[1]]) {
        state.chatLog[r[0]][r[1]].rows.push(data)
        state.chatLog[r[0]][r[1]].hasNew = true
        state.chatLog[r[0]][r[1]].newCount ++
      }
      else state.chatLog[r[0]][r[1]] = {rows: [data], noMore: false,hasNew: true,newCount: 1}
    },
    appendChatLog(state, data) {
      let r = data['tableName'].split('_')
      if (state.chatLog[r[0]][r[1]]) {
        state.chatLog[r[0]][r[1]].rows = [...data.rows, ...state.chatLog[r[0]][r[1]].rows]
        state.chatLog[r[0]][r[1]].noMore = data.rows.length < 20
      } else state.chatLog[r[0]][r[1]] = {rows: [data], noMore: false,hasNew: false}
    }
  }
})



